import React, { Component } from 'react'
import {
    // CBadge,
    CButton,
    // CButtonGroup,
    CCard,
    CCardBody,
    // CCardFooter,
    CCardHeader,
    CCol,
    CRow,

    CContainer,
    CForm,
    CFormGroup,
    CSwitch,
} from '@coreui/react'


import "grapesjs/src/styles/scss/main.scss";
import 'grapesjs/dist/css/grapes.min.css';

// import grapesjs from 'grapesjs';
// import { Editor } from 'grapesjs-react';

import Axios from '../../lib/api';

import swal from 'sweetalert';

class LeadNew extends Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: false,


            name: true,


            email: false,
            message: false,
            phone_number: false,
            page_list: []//page list
        }
        var user_id = props.match.params._id;
        if (parseInt(user_id) > 0) this.user_id = user_id;
        console.log(this.user_id, props)
    }
    componentDidMount = () => {
        if (this.user_id) {
            Axios.get('admin/page/pages').then(r => {
                console.log(r)
                this.setState({ page_list: r.data })
            })
            Axios.get(`admin/users/${this.user_id}`).then(r => {
                console.log(r.data.data[0])
                delete r.data.data[0].password
                var user = r.data.data[0];
                this.setState({
                    first_name: user.first_name || '',
                    last_name: user.last_name || '',
                    company_name: user.company_name || '',
                    email: user.email || '',
                    // id: user.id,
                })
            })
        }

    }
    onBack() {
        this.props.history.push('/admin/leads');
    }

    onCheckboxChange(e, name) {
        const value = e.target.type === 'checkbox' ? e.target.checked : e.target.value;
        this.setState({ [name]: value })
    }

    onTapRegister() {
        this.setState({ loading: true })
        var data = this.state;
        var url = 'admin/lead/add-lead';
        if (this.user_id) {
            data.id = this.user_id
        }


        Axios.post(url, data).then(r => {
            console.log(r.data)
            swal({
                title: data.id == null ? "Successfully created." : "Successfully updated.",
                // text: "Check your email",
                icon: "success",
                buttons: {
                    confirm: {
                        text: "OK",
                        value: true,
                        visible: true,
                        className: "",
                        closeModal: true
                    },
                }

            }).then(() => {
                this.props.history.push('/admin/leads');
            })

            this.setState({ loading: false })
        })
            .catch(e => {
                console.log(e)
                this.setState({ loading: false })
            })
    }
    render() {
        var { loading } = this.state;
        return (
            <>
                <CRow>
                    <CCol>
                        <CCard>
                            <CCardHeader>{this.user_id ? 'Edit Lead' : 'New Lead'}</CCardHeader>
                            <CCardBody>
                                <CContainer>
                                    <CRow className="justify-content-center">
                                        <CCol md="9" lg="7" xl="6">
                                            <CCard className="mx-4">
                                                <CCardBody className="p-4">
                                                    <CForm className="was-validated1">
                                                        <CFormGroup row>
                                                            <div className="col-6 col-form-label">Name</div>
                                                            <div className="col-6"><CSwitch color={'success'} defaultChecked={this.state.name} onChange={(e) => this.onCheckboxChange(e, 'name')} /></div>
                                                        </CFormGroup>
                                                        <CFormGroup row>
                                                            <div className="col-6 col-form-label">Phone Number</div>
                                                            <div className="col-6"><CSwitch color={'success'} defaultChecked={this.state.phone_number} onChange={(e) => this.onCheckboxChange(e, 'phone_number')} /></div>
                                                        </CFormGroup>
                                                        <CFormGroup row>
                                                            <div className="col-6 col-form-label">Email</div>
                                                            <div className="col-6"><CSwitch color={'success'} defaultChecked={this.state.email} onChange={(e) => this.onCheckboxChange(e, 'email')} /></div>
                                                        </CFormGroup>
                                                        <CFormGroup row>
                                                            <div className="col-6 col-form-label">Message</div>
                                                            <div className="col-6"><CSwitch color={'success'} defaultChecked={this.state.message} onChange={(e) => this.onCheckboxChange(e, 'message')} /></div>
                                                        </CFormGroup>


                                                        <CButton color="success" block onClick={() => { this.onTapRegister(); }}>{this.user_id ? 'Update Lead' : 'Create Lead'}
                                                            {loading && <span className="spinner-border spinner-border-sm ml-1" role="status" aria-hidden="true"></span>}
                                                        </CButton>
                                                    </CForm>
                                                </CCardBody>
                                            </CCard>
                                        </CCol>
                                    </CRow>
                                </CContainer>

                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </>
        )
    }
}

export default LeadNew
