import React from 'react';
import PageList from './views/page/PageList';
import PageView from './views/page/PageView';
import LeadNew from './views/lead/LeadNew';

const Dashboard = React.lazy(() => import('./views/admin/page/Dashboard'));
const AdminPageList = React.lazy(() => import('./views/admin/page/PageList'));
const New = React.lazy(() => import('./views/admin/page/New'));
const NewBasic = React.lazy(() => import('./views/admin/page/NewBasic'));
const NewComing = React.lazy(() => import('./views/admin/page/NewComing'));
const NewComing2 = React.lazy(() => import('./views/admin/page/NewComing2'));


const Edit = React.lazy(() => import('./views/admin/page/Edit'));
const View = React.lazy(() => import('./views/admin/page/View'));
const WidgetList = React.lazy(() => import('./views/admin/widgets/WidgetList'));
const WidgetView = React.lazy(() => import('./views/admin/widgets/WidgetView'));
const WidgetViewWhatsapp = React.lazy(() => import('./views/admin/widgets/WidgetViewWhatsapp'));
const WidgetViewCallButton = React.lazy(() => import('./views/admin/widgets/WidgetViewCallButton'));
const WidgetViewPoweredBy = React.lazy(() => import('./views/admin/widgets/WidgetViewPoweredBy'));



const Setting = React.lazy(() => import('./views/admin/setting/setting'));


const Users = React.lazy(() => import('./views/admin/Users/Users'));
const UserEdit = React.lazy(() => import('./views/admin/Users/UserEdit'));
const UserNew = React.lazy(() => import('./views/admin/Users/UserNew'));

const LeadList = React.lazy(() => import('./views/lead/LeadList'));
const LeadView = React.lazy(() => import('./views/lead/LeadView'));


const routes = [
  { path: '/', exact: true, name: 'Home' },
  
  { path: '/', name: 'Page', component: Dashboard, exact: true },
  { path: '/dashboard', name: 'Page', component: Dashboard, exact: true },
  { path: '/pages', name: 'Page', component: AdminPageList, exact: true },
  { path: '/new', name: 'New', component: New, exact: true },
  { path: '/new/basic', name: 'New', component: NewBasic, exact: true },
  { path: '/new/basic_plus', name: 'New', component: NewBasic, exact: true },
  
  { path: '/new/coming_soon', name: 'New', component: NewComing, exact: true },
  { path: '/new/coming_soon2', name: 'New', component: NewComing2, exact: true },

  { path: '/new-builder', name: 'New', component: Edit, exact: true },
  { path: '/new-builder/:id', name: 'New', component: Edit, exact: true },
  
  { path: '/edit/:_id', name: 'New', component: New, exact: true },
  { path: '/view/:_id', name: 'View', component: View, exact: true },

  { path: '/users', name: 'New', component: Users, exact: true },
  { path: '/users/new', name: 'New User', component: UserNew, exact: true, },
  { path: '/users/:_id', name: 'User Details', component: UserEdit, exact: true, },

  { path: '/leads', name: 'Lead', component: LeadList, exact: true },
  { path: '/leads/new', name: 'Lead New', component: LeadNew, exact: true, },
  { path: '/leads/view/:_id', name: 'Lead Details', component: LeadView, exact: true, },
  

  { path: '/widget-setting', name: 'Widget Setting', component: WidgetList, exact: true },
  
  { path: '/widget-setting/whatsapp', name: 'View', component: WidgetViewWhatsapp, exact: true },
  { path: '/widget-setting/call-button', name: 'View', component: WidgetViewCallButton, exact: true },
  { path: '/widget-setting/powered-by', name: 'View', component: WidgetViewPoweredBy, exact: true },
  
  
  { path: '/widget-setting/:id', name: 'View', component: WidgetView, exact: true },
  
  { path: '/setting', name: 'Wetting', component: Setting, exact: true },

  // { path: '/page', name: 'PageList', component: PageList, exact: true },
  // { path: '/page/view/:_id', name: 'View', component: PageView, exact: true },

];

export default routes;
