import React, { Component } from 'react'
import {

  CCard,
  CCardBody,

  CCardHeader,
  CCol,
  CButton,
  CRow

} from '@coreui/react'

import swal from 'sweetalert';


import * as moment from 'moment'
import Axios from '../../lib/api';
import './PageList.css'
const REACT_APP_BASE_URL = require('../../lib/env').default;

class PageList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list: []
    }
  }
  componentDidMount = () => {
    Axios.get('admin/page/pages').then(r => {
      console.log(r)
      var page = r.data;
      var user = JSON.parse(localStorage.getItem("user"));
      console.log(user)
      if (user == null) return;

      if (user.role_id === -1) {
        this.setState({ list: page });
        return;
      }

      if (user.user_pages == null) return;


      page = page.filter(p => {
        var matchUP = user.user_pages.find(up => {

          return up.page_id === p.id
        });
        console.log(matchUP)
        return matchUP != null
      });
      console.log(page)
      page = page.map(p => {
        var up = user.user_pages.find(up => up.page_id === p.id);
        if (up == null) return p;
        p.view = up.view
        p.lead = up.lead
        p.edit = up.edit

        return p;
      });
      console.log(page)
      this.setState({ list: page });
    })
  }
  onVisit(id) {
    var host = REACT_APP_BASE_URL.replace('api/', '');

    window.open(`${host}p/${id}`, '_blank');
  }
  onView(id) {
    this.props.history.push(`/page/view/${id}`);
  }
  onEdit(id) {
    this.props.history.push(`/admin/edit/${id}`);
  }
  onDelete(item) {
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this page!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    })
      .then(willDelete => {
        if (willDelete)
          Axios.delete(`admin/page/page/${item.id}`).then(r => {
            console.log(r)
            var { list } = this.state;
            list = list.filter(page => page.id !== item.id);
            this.setState({ list: list })
          })
      })

  }
  onDuplicate(item) {
    try {
      item = JSON.parse(JSON.stringify(item))
      delete item.id
      Axios.post('admin/add-page', item).then(r => {
        console.log(r)
        this.props.history.push(`/admin/edit/${r.data.id}`);
      })
    } catch (e) {
      console.log(e)
    }
  }
  onExport() {
    window.open(`admin/page/pages-export`, '_blank');
  }
  render() {
    var { list } = this.state;
    return (
      <>
        <CRow>
          <CCol>
            <CCard>
              <CCardHeader>
                Page List
                
              </CCardHeader>
              <CCardBody>


                <table className="table table-hover table-outline mb-0 d-none d-sm-table">
                  <thead className="thead-light">
                    <tr>
                      <th>Name</th>
                      <th>Visit Number</th>
                      <th className="text-right">View</th>
                    </tr>
                  </thead>
                  <tbody>
                    {list.map(page => {
                      var date = moment(new Date(page.createdAt)).format('DD MMMM YYYY')
                      return <tr key={page.id}>

                        <td>
                          <div>{page.title}</div>
                          <div className="small text-muted">
                            <span>New</span> | Registered: {date}
                          </div>
                        </td>
                        <td>{page.visit}</td>

                        <td className="text-right">
                          <CButton color="primary" disabled={page.view !== true} size="sm" className="custom" variant="outline" onClick={() => this.onVisit(page.id)}>Visit</CButton>
                          <CButton color="success" disabled={page.view !== true} size="sm" className="" variant="outline" onClick={() => this.onView(page.id)}>View</CButton>
                          <CButton color="primary" disabled={page.edit !== true} size="sm" className="" variant="outline" onClick={() => this.onEdit(page.id)}>Edit</CButton>

                          {/* <button className="small btn btn-danger" onClick={() => this.onDelete(page)}>Delete</button> */}
                          {/* <button className="small btn btn-info" onClick={() => this.onDuplicate(page)}>Duplicate</button> */}
                        </td>
                      </tr>

                    })}

                  </tbody>
                </table>

              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    )
  }
}

export default PageList
